<template>
  <div>
    <table>
      <thead>
      <tr>
        <th>Name</th>
        <th>Brand</th>
        <th>Weight</th>
        <th>Price</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item, index) in paginatedData" :key="index">
        <td>{{item.name}}</td>
        <td>{{item.brand}}</td>
        <td>{{item.weight}}</td>
        <td>{{item.price}}</td>
      </tr>
      </tbody>
    </table>
    <div class="pagination">
        <div class="pagination_left" v-show="pageNumber > 0">
          <a href="#"
             @click="prevPage">Предыдущая</a>
        </div>
        <div class="pagination_mid">
          <span>{{pageNumber + 1}} из {{pageCount + 1}}</span>
        </div>
        <div class="pagination_right" v-show="pageNumber <= pageCount - 1">
          <a href="#"
             @click="nextPage">Следующая</a>
        </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'Pagination',
    data() {
      return {
        pageNumber: 0,
      }
    },
    props: {
      listData: {
        type: Array,
        required: true,
      },
      size: {
        type: Number,
        required: false,
        default: 5,
      },
    },
    methods: {
      nextPage() {
        this.pageNumber++;
      },
      prevPage() {
        this.pageNumber--;
      },
    },
    computed: {
      pageCount() {
        let len = this.listData.length;
        let size = this.size;
        return Math.floor(len / size);
      },
      paginatedData() {
        const start = this.pageNumber * this.size;
        const end = start + this.size;
        return this.listData.slice(start, end);
      }
    }
  }
</script>

<style scoped>


  .pagination {
    width: 80%;
    display: flex;
    margin: 0 auto;
    align-items: center;
    padding: 0 15px;
  }

  .pagination_left, .pagination_right {
    width: 30%;
  }

  .pagination a, .pagination span {
    display: block;
    text-align: center;
    font-family: Helvetica, Arial, sans-serif;
    font-weight: 300;
    line-height: 42px;
    color: #999;
    font-size: 16px;
  }

  .pagination a {
    padding: 0 20px;
    background-color: #4CAF50;
    color: #fff;
    border-radius: 4px;
    border: 1px solid #ccc;
    text-decoration: none;
    margin: 0 6px;
    transition: all .2s ease-in-out;
  }


  @media (hover) {
    .pagination a:hover {
      background-color: #45a049;
    }
  }

  .pagination_mid {
    display: flex;
    justify-content: center;
    width: 60%;
  }

</style>
