<template>
  <div id="app">
    <div class="search-wrap">
      <input type="text" v-model="search" placeholder="Search...">
      <select name="sort" v-model="sort">
        <option v-for="(option, index) in options" :value="option.value" :key="index">{{option.label}}</option>
      </select>
      <input type="button" @click="resetOptions" value="Сбросить фильтры">
    </div>
    <Pagination :list-data="filteredList"></Pagination>
  </div>
</template>

<script>
  import Pagination from './components/Pagination.vue'
  import goodsList from './api/data';

  export default {
    name: 'app',
    components: {
      Pagination
    },
    data() {
      return {
        search: '',
        sort: '',
        options: [
          {label: 'Default', value: 'none'},
          {label: 'Brand', value: 'brand'},
          {label: 'Price', value: 'price'}
        ],
        goods: goodsList,
        totalPhotos: 0,
        perPage: 5,
        currentPage: 1,
      }
    },
    methods: {
      resetOptions() {
        this.search = '';
        this.sort = 'none';
      }
    },
    computed: {
      filteredList() {
        let filteredGoods = this.goods.filter(item => {
          return item.name.toLowerCase().includes(this.search.toLowerCase());
        });
        switch (this.sort) {
          case 'brand':
            filteredGoods.sort((a, b) => a.brand.localeCompare(b.brand));
            break;
          case 'price':
            filteredGoods.sort((a, b) => parseInt(a.price - b.price));
            break;
        }
        return filteredGoods;
      }
    },
  }
</script>

